	module.exports = {"data":{"markdownRemark":{"html":"<p>To untrack a <em>single</em> file, ie stop tracking the file but not delete it from the system use:</p>\n<p><code>git rm --cached filename</code></p>\n<p>To untrack <em>every</em> file in <code>.gitignore</code>:</p>\n<p>First <strong>commit</strong> any outstanding code changes, and then run:</p>\n<p><code>git rm -r --cached</code></p>\n<p>This removes any changed files from the index(staging area), then run:</p>\n<p><code>git add .</code></p>\n<p>Commit it:</p>\n<p><code>git commit -m \".gitignore is now working\"</code></p>\n<p>To undo <code>git rm --cached filename</code>, use <code>git add filename</code></p>","fields":{"slug":"/miscellaneous/untrack-files-previously-committed-from-new-gitignore/"},"frontmatter":{"title":"Untrack Files Previously Committed from New Gitignore"}}},"pathContext":{"slug":"/miscellaneous/untrack-files-previously-committed-from-new-gitignore/"}}

